<template>
    <div class="max-w-lg mt-8 mx-auto p-4 bg-gray-200 rounded">
        <label for="" class="text-2xl">Role Assign</label>
        <div class="text-left p-2"> 
            <label for="">Role</label>
            <Select class="w-full border py-3 bg-white outline-none p-2" v-model="model_id">
                <option value=""> Select User </option>
                <option :value="index" v-for="(user, index) in mentorAssigns.users" :key="index">{{ user }}</option>
            </Select>
        </div>
        <div class="text-left p-2">
            <label for="">Role</label>
            <Select class="w-full border py-3 bg-white outline-none p-2" v-model="role_id">
                <option value=""> Select Role </option>
                <option :value="role.id" v-for="role in mentorAssigns.roles" :key="role.id">{{ role.name }} </option>
            </Select>
        </div>
        <div class="mt-2">
            <Btn type="submit">Submit</Btn>
        </div>
    </div>
</template>
<script>
import Select from "../../../components/select.vue";
import Btn from "../../../components/btn.vue";
export default {
    components:{
        Select,
        Btn
    },
     data(){
        return {
            model_id:'',
            role_id:''
        }

    },
    created(){
        this.$store.dispatch('adminMentorAssign/assignMentor');
    },
    computed:{
        mentorAssigns(){
            return this.$store.state.adminMentorAssign.assignMentor;
        },
        patients(){
            return  Object(this.mentorAssigns.appointed)
        }
    }
}
</script>